/* ================================= RESET ================================= */
@import reset

/* ================================= VENDOR ================================= */
@import _vendor

/* ================================= VARIABLES ================================= */
@import _variabless

/* ================================= PRESETS ================================= */
@import _presets

// html
//     font-size: 14px
body
    height: 100%
    background-color: $color-white
    font-family: $roboto

.icon-counter
    position: relative
    &__counter
        position: absolute
        left: 50%
        top: 17%

// BURGER
.burger
    z-index: 11
    width: 25px
    cursor: pointer
    z-index: 10
    & span
        display: block
        transition: 0.3s
        height: 1px
        background-color: $color-black
        cursor: pointer
        margin-bottom: 8px
        &:last-child
            margin: 0
    &_active
        span
            transition: 0.3s
            margin: 0
            &:first-child
                transform: rotate(45deg)
            &:last-child
                transform: rotate(-45deg)
                margin-top: -1px

    &-network
        color: $color-gray
        cursor: pointer
        padding: 10px
        z-index: 10
        &:hover
            color: $color-dark-gray
        &_active
            color: $color-gray

// HEADER
.header
    position: fixed
    left: 0
    right: 0
    top: 0
    background-color: $color-white
    height: 60px
    padding: 0 30px
    display: grid
    grid-template-columns: repeat(3,1fr)
    align-items: center
    z-index: 10
    &__menu
        justify-self: start
    &__link
        font-family: $roboto
        text-transform: uppercase
        letter-spacing: 1.7px
        margin-right: 35px
        &:last-child
            margin: 0
    & a
        &:hover
            color: $color-dark-gray
    &__logo
        place-self: center
    &__inner
        justify-self: end
        display: grid
        grid-template-columns: 1fr auto
        gap: 20px
        align-items: center
    &__icon
        display: inline-block
        margin-right: 20px
        &:last-child
            margin: 0
    &__scroll
        position: absolute
        bottom: 0
        left: 0
        height: 1px
        background-color: $color-dark-gray
        width: 0%
        z-index: 10
        -webkit-transition: 0.1s
        transition: 0.1s
        mix-blend-mode: difference

.header-mobile
    display: none

// MAIN-SCREEN
.main-screen
    background: url("../img/main-screen.png") center / cover no-repeat
    min-height: 770px
    color: $color-white
    text-align: center
    display: flex
    flex-direction: column
    justify-content: flex-end
    margin-top: 60px
    &__title
        line-height: 1.3
        margin-bottom: 10px
    &__subtitle
        margin-bottom: 205px
    &__buttons
        margin-bottom: 75px

.buttons-wrap
    &__btn
        margin-right: 25px
        &:hover
            background-color: $color-dark-gray
            color: $color-white
// SECTION-MODULE
.section-module
    &__title
        margin-bottom: 25px
    &__body
        display: grid
        grid-template-columns: repeat(3,1fr)
        gap: 25px
    & .post
        transition: 0.5s
        &:hover
            transform: translateY(-20px)
            transition: 0.5s

// TABS
.tabs
    &__tabs
        display: flex
        justify-content: center
        border-bottom: 2px solid $color-gray
        margin-bottom: 35px
    &__tab
        width: 125px
        margin-right: 80px
        letter-spacing: 2px
        &_active
            border-bottom: 2px solid $color-black
    &__content
        display: none
        grid-template-columns: repeat(3,1fr)
        gap: 25px
        &_active
            display: grid

// POST
.post
    &__inner
        position: relative
        height: 396px
        max-height: 396px
        display: flex
        flex-direction: column
        justify-content: center
        align-items: center
        margin-bottom: 17px
        overflow: hidden
    &__img
        position: absolute
        left: 0
        top: 0
        z-index: -1
        transition: 0.5s
    &__btn
        width: 275px
        margin: 0 0 12px 0
        letter-spacing: 2px
        opacity: 0
        transform: translateX(20px)
        transition: 0.5s
    &:hover
        transition: 0.5s
        .post__btn
            opacity: 1
            transform: translateX(0px)

        .post__img

            transform: scale(1.1)
    &__name
        @include responsive-font($d-h3, $m-h3)
        letter-spacing: 0.5px
        padding-bottom: 10px
        margin-bottom: 10px
        border-bottom: 1px solid $color-gray
        display: block
        line-height: 1.4
        &_not-margin
            margin: 0
        &_not-border
            border: 0
    &__info
        padding: 0 30px
    &__description
        display: flex
        align-items: center
        margin-bottom: 18px
    &__icon
        width: 30px
        height: 30px
        display: inline-block
        margin-right: 10px

// SECTION-INFO
.section-info
    &__mediafile
        width: 100%
        // min-height: 50vh
        max-height: 70vh
        object-fit: cover
        margin-bottom: 50px
    &__body
        display: grid
        place-items: center
        text-align: center
        gap: 30px
    &__subtitle
        line-height: 2
    &__btn
        &:hover
            background-color: $color-dark-gray
            color: $color-white
        &_wide
            width: 385px

// SECTION-SLIDER
.section-slider
    padding-left: 90px
    &__title
        margin-bottom: 25px
    &__navigation
        position: relative
        z-index: 1
// FOOTER
.footer
    padding: 70px 55px 90px
    background-color: $color-black
    color: $color-white
    & a
        &:hover
            color: $color-gray
    &-strong
        margin-bottom: 20px
        text-transform: uppercase
        letter-spacing: 2px
        display: inline-block
    &__form
        display: inline-block
        margin-bottom: 90px
        &-span
            margin-bottom: 5px
            display: inline-block
        &-inner
            display: flex
        &-input
            max-width: 302px
            width: 100%
            height: 46px
            border-bottom: 1px solid $color-white
            padding: 0 10px
            background-color: $color-dark-gray
            color: $color-white

            margin-right: 10px
            &:focus
                outline: 1px solid $color-white
        &-btn
            width: 130px

    &__menu
        display: grid
        grid-template-columns: repeat(4,1fr)
        margin-bottom: 70px
        &-inner
            display: flex
            flex-direction: column
        &-item
            margin-bottom: 15px
        &-link
            color: $color-white
            @include responsive-font($d-display-12, $m-display-12)
            letter-spacing: 0.5px
    &__bottom
        display: grid
        grid-template-columns: 1fr 1fr 2fr
        margin-bottom: 90px
    &__description
        margin-bottom: 15px
    &__networks
        &-link
            margin-right: 15px
    &__logo
        place-self: center
    &__flags
        align-self: start
        display: flex
        flex-wrap: wrap
    &__flag
        margin: 0 25px 10px 0
        &-img
            width: 37px
            height: 22px
        &:last-child
            margin: 0
    &__address
        letter-spacing: 0.5px

@media (max-width: 1091px)
    .header
        display: none
    .header-mobile
        display: grid
        grid-template-columns: auto 1fr
        position: fixed
        gap: 20px
        align-items: center
        padding: 10px 30px
        background-color: $color-white
        left: 0
        right: 0
        top: 0
        z-index: 10

        &__logo-img
            width: 100%
        &__icons
            display: flex
            justify-self: end
            align-items: center
        &__icon
            margin-right: 20px
            &:last-child
                margin: 0
        &__nav
            position: fixed
            display: block
            left: 0
            right: 0
            top: 60px
            z-index: 11
            background-color: $color-black
            transition: 0.3s
            opacity: 0
            height: 0
            &_active
                opacity: 1
                transition: 0.3s
                height: calc(100% - 60px)
        &__list
            display: grid
            place-content: center
            gap: 40px
            text-align: center
            height: 100%
        &__link
            color: $color-white
            text-transform: uppercase
            @include responsive-font(28, 16)
            letter-spacing: 0.5px

    .post
        &__inner
            max-height: 295px
            height: 295px
        &__btn
            width: 90%
    .footer
        &__form
            display: block

@media (max-width: 768px)
    .m-bottom
        margin-bottom: 60px
    .burger
        display: block

    .icon-counter
        &__counter
            top: 20%

    .header
        &__inner
            gap: 0
    .buttons-wrap
        &__btn
            display: flex
            flex-direction: column
            margin: 0 0 15px 0
            width: 100%
            &:last-child
                margin: 0
    .main-screen
        min-height: calc(100vh - 60px)
        background-position: right
        &__subtitle
            margin-bottom: 120px
    .tabs
        &__tab

            width: 100%
            margin: 0
        &__content
            grid-template-columns: repeat(2,1fr)

    .post
        &__info
            padding: 0
        &__inner
            height: 25vh

    .section-info
        &__body
            place-items: inherit
        &__btn
            &_wide
                width: 100%

    .section-slider
        padding: 0 15px

    .section-module
        &__body
            grid-template-columns: repeat(2,1fr)

    .footer
        &__form
            margin-bottom: 40px
            &-inner
                flex-direction: column
            &-input
                width: 100%
                max-width: 100%
                margin: 0 0 15px 0
            &-btn
                width: 100%
        &__menu,
        &__bottom
            grid-template-columns: 1fr
            gap: 30px
