a
    text-decoration: none
    color: $color-black
button,
input
    outline: 0
    border: none
    box-sizing: border-box

.container
    max-width: 1265px
    margin: 0 auto
    padding: 0 15px

.m-bottom
    margin-bottom: 120px

.btn-light,
.btn-dark,
.btn-gray
    width: 180px
    height: 48px
    padding: 0
    @include responsive-font($d-display-14, $m-display-12)
    display: inline-flex
    justify-content: center
    align-items: center
    text-align: center
    font-family: $roboto
    text-transform: uppercase
    border-radius: 2px
    letter-spacing: 2px
    cursor: pointer
    &:last-child
        margin: 0
.btn
    &-light
        background-color: $color-white
        color: $color-black
        &:hover
            background-color: $color-black
            color: $color-white
    &-dark
        background-color: $color-black
        color: $color-white
        &:hover
            background-color: $color-white
            color: $color-black
    &-gray
        background-color: $color-dark-gray
        color: $color-white
        &:hover
            background-color: $color-white
            color: $color-black

.img-cover
    width: 100%
    height: 100%
    object-fit: cover

.visually-hidden
    position: absolute
    clip: rect(1px, 1px, 1px, 1px)
    padding: 0
    border: 0
    height: 1px
    width: 1px
    overflow: hidden
